<template>
  <div class="login">
    <div class="login_box">
      <!-- 头像区域 -->
      <div class="zjy_d1">
        <img src="../assets/logo.png" alt />
      </div>
      <!-- 登陆表单区域 -->
      <el-form
        :rules="loginFormRules"
        :model="loginForm"
        ref="loginFromRef"
        label-width="0px"
        class="login_form"
      >
        <!-- 用户名 -->
        <el-form-item prop="username">
          <el-input
            v-model="loginForm.username"
            prefix-icon="iconfont icon-user"
          ></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="password">
          <el-input
            type="password"
            v-model="loginForm.password"
            prefix-icon="iconfont icon-3702mima"
          ></el-input>
        </el-form-item>
        <!-- 按钮区域 -->
        <el-form-item class="btns">
          <el-button type="primary" @click="zjy_login">登录</el-button>
          <el-button type="info" @click="zjy_reset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 这是登录表单的数据绑定对象
      loginForm: {
        username: 'admin',
        password: '123456'
      },
      // 这是表单的验证规则对象
      loginFormRules: {
        // 验证用户名是否合法
        username: [
          { required: true, message: '请输入登录名称', trigger: 'blur' },
          { min: 3, max: 10, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        // 验证密码是否合法
        password: [
          { required: true, message: '请输入登录密码', trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  created() {},
  mounted() {},
  methods: {
    // 点击重置按钮 重置登录表单
    zjy_reset() {
      // console.log(this)
      this.$refs.loginFromRef.resetFields()
    },
    // 点击登录
    zjy_login() {
      this.$refs.loginFromRef.validate(async valid => {
        console.log(valid)
        if (!valid) return
        let { data: res } = await this.$http.post('login', this.loginForm)
        console.log(res)
        if (res.meta.status !== 200) {
          return this.$message.error('登陆失败')
        } else {
          this.$message.success('登陆成功')
          // 1将登陆成功之后的token，保存到客户端的 sessionStorage中
          // 1.1 项目中出了登陆之外的其他API接口，必须在登录之后才能访问
          // 1.2 token 只应在当前网站打开期间有效，所以将token保存在sessionStorage 中
          console.log(res)
          window.sessionStorage.setItem('token', res.data.token)
          // 2.通过编程式导航跳转到后台主页，路由地址是/home
          this.$router.push({
            path: '/home'
          })
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
.login {
  background: #2b4b6b;
  height: 100%;
}
.login_box {
  width: 450px;
  height: 300px;
  background: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  .zjy_d1 {
    width: 130px;
    height: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;

    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background: #eee;
    }
  }
}
.btns {
  display: flex;
  justify-content: flex-end;
}
.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}
</style>
